<template>
	<div class="screen-loader" v-if="visible">
		<Loader :start-with-loading="true" />
	</div>
</template>

<script>
	import Loader from "../widgets/Loader"

	export default {
		name: "ScreenLoader",

		data() {
			return {
				visible: false,
			}
		},

		methods: {
			/**
			 */
			show() {
				this.visible = true;
			},
			/**
			 */
			hide() {
				this.visible = false;
			},
		},

		created() {
			app.setComponent('$loader', this);
		},

		components: {
			'Loader': Loader,
		},
	}
</script>

<style lang="less" scoped>
	.screen-loader {
		display: flex;
		justify-content: center;
		align-items: center;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0, 0, 0, .75);
		z-index: 1;
	}
</style>


